import { SessionsManagerService } from 'src/app/services/sessions-manager-service';
import { IonRefresher } from '@ionic/angular';
import { Session } from 'src/app/models/session';
import { Component, OnInit } from '@angular/core';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';

@UntilDestroy()
@Component({
  selector: 'app-pvd-active-requests',
  templateUrl: './pvd-active-requests.page.html',
  styleUrls: ['./pvd-active-requests.page.scss'],
})
export class PvdActiveRequestsPage implements OnInit {

  refresher:IonRefresher = null;
  constructor(
    public sessionsManager:SessionsManagerService
  ) { }

  ngOnInit() {
    this.sessionsManager.myActiveRequestsShared
      .getUpdateNotifier(this)
      .subscribe((result) => {
        this.refresher && this.refresher.complete();
        
      });
      this.sessionsManager.myActiveRequestsShared.request();
  }
  refresh(event) {
    this.sessionsManager.myActiveRequestsShared.request();
    this.refresher = event.target;
  }
  ngOnDestroy(): void {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.
    
  }

}
